/*reviews*/
.reviews {
  padding: 100px 0;
  position: relative;
  width: 100%;
  margin-bottom: 100px;
  z-index: 10;
  background-color: #f8f4f0;
}

.reviews__title {
  margin: 0;
  margin-bottom: 20px;
  color: #1e3a2b;
  font-size: 60px;
  font-family: "GraphikLCTT", Arial, sans-serif;
  line-height: 0.9;
  font-weight: 600;
  background-position: center center;
  border-color: transparent;
  border-style: solid;
}

.reviews img {
  width: 100%;
  display: flex;
  align-self: center;
}
