.reviews__container-wrapper {
  overflow: hidden;
}

.reviews__container {
  position: relative;
  height: 330px;
  width: 100%;
}

.reviews .title {
  margin-bottom: 40px;
  text-align: center;
}

.reviews__list {
  position: absolute;
  left: 0;
  top: 0;
}

.reviews__animation {
  transform: translate(0%, 0%);
  animation: reviews__animation 45s linear infinite;
  will-change: transform;
}

@keyframes reviews__animation {
  from {
    transform: translate(0%, 0%);
  }

  to {
    transform: translate(-50%, 0%);
  }
}

@media screen and (max-width: 750px) {
  .main-themes .splide__list {
    margin: 40px 0 0 0 !important;
    display: flex !important;
    flex-wrap: nowrap;
    max-width: initial;
    width: 100%;
    gap: initial;
  }

  .main-themes__block {
    padding: 16px;
    margin: 0;
  }

  .book__block {
    flex-direction: column;
  }

  .book__text {
    margin: 0;
  }
}

.reviews__line {
  display: flex;
  width: max-content;
  overflow: auto;
  margin: 24px 0 0 0;
}

.reviews__block {
