.reviews__name {
  font-weight: 700;
  font-size: 16px;
  line-height: 23px;
  color: #ffffff;
}

.reviews__text {
  margin: 16px 0 0 0;
  font-size: 14px;
  line-height: 130%;
  color: #fff;
}

@media screen and (max-width: 1350px) {
  .reviews {
    margin: 0 0 80px 0;
  }

  .reviews .title {
    margin-bottom: 36px;
  }

  .reviews__container {
    transform: translate(-10%, -10%) scale(0.8);
  }
}

@media screen and (max-width: 980px) {
  .reviews {
    margin: 0;
  }

  .reviews .title {
    margin-bottom: 26px;
  }

  .reviews__container {
    transform: translate(-13%, -10%) scale(0.75);
  }
}

@media screen and (max-width: 750px) {
  .reviews .title {
    margin-bottom: 16px;
  }
}

@media screen and (max-width: 581px) {
  .reviews .title {
    text-align: left;
  }
}
