.who-use {
  margin-bottom: 100px;
  background-color: #f8f4f0;

	&__container {
		padding: 100px 0;
	}
	
  &__title {
    margin: 0;
    margin-bottom: 40px;
    max-width: 470px;
    color: #1e3a2b;
    font-size: 60px;
    font-family: "GraphikLCTT", Arial, sans-serif;
    line-height: 0.9;
    font-weight: 600;
    background-position: center center;
    border-color: transparent;
    border-style: solid;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: repeat(6, 1fr);
    gap: 24px;
  }

  &__item {
    position: relative;
    border-radius: 20px;
    padding: 25px;
    background-color: #1e3a2b;
    display: flex;
    flex-direction: column;
    justify-content: end;

    &--big {
      position: relative;
      display: flex;
      flex-direction: column;
      justify-content: end;
      grid-column: span 6 / span 6;
      grid-row: span 6 / span 6;
      height: 100%;
      min-height: 570px;
    }

    &--smal1 {
      grid-column: span 6 / span 6;
      grid-column-start: 7;
      grid-row: span 3 / span 3;
    }

    &--smal2 {
      grid-column: span 6 / span 6;
      grid-column-start: 7;
      grid-row: span 3 / span 3;
    }
  }

  &__icon {
    position: absolute;
    left: 35px;
    top: 35px;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #ffffff;
    width: 50px;
    height: 50px;
  }

  &__img {
    position: absolute;
    right: 25px;
    top: 25px;
    display: flex;
    margin-left: auto;
    width: 260px;
  }

  &__sub-title {
    margin: 0;
    color: #ffffff;
    font-size: 24px;
    font-family: "GraphikLCTT", Arial, sans-serif;
    line-height: 1;
    font-weight: 500;
    background-position: center center;
    border-color: transparent;
    border-style: solid;
  }

  &__descr {
    margin: 0;
    color: #ffffff;
    font-size: 18px;
    font-family: "GraphikLCTT", Arial, sans-serif;
    line-height: 1.4;
    font-weight: 400;
    opacity: 0.6;
    background-position: center center;
    border-color: transparent;
    border-style: solid;
  }
}
