.basic-programme {
  margin-bottom: 100px;
}

.collapse {
  overflow: hidden;
  transition: 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  transition-property: opacity, height;
  will-change: opacity, contents;
}

.basic-programme__title {
  margin: 0;
  margin-bottom: 40px;
  color: #1e3a2b;
  font-size: 60px;
  font-family: "GraphikLCTT", Arial, sans-serif;
  line-height: 0.9;
  font-weight: 600;
  background-position: center center;
  border-color: transparent;
  border-style: solid;
}

.collapse:not(.is-active) {
  height: 0;
  opacity: 0;
}

.accordion {
  gap: 20px;
  // --background: #f3f3f3;
  // --border-radius: 12px;
  // border-radius: var(--border-radius);
  // background: var(--background);
  // box-shadow: 0 2px 6px rgba(0, 0, 0, 0.075);
  // border: 1px solid #d9d9d9;
}
.accordion .card {
  overflow: hidden;
  margin: -1px -1px 1px;
  margin-bottom: 10px;
  padding: 20px;
  background: #f8f4ee;
  border-radius: 20px;
  // border: 1px solid #d9d9d9;
  transition: 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  will-change: contents;
  text-align: left;
}
.accordion .card:first-child {
  // border-radius: var(--border-radius) var(--border-radius) 0 0;
  border-radius: 20px;
}
.accordion .card:last-child {
  // border-radius: 0 0 var(--border-radius) var(--border-radius);
  border-radius: 20px;
  margin-bottom: -1px;
}
.accordion .card:not(:last-child):not(.is-active) {
  border-bottom-color: transparent;
}
.accordion .card.is-active {
  background: #f8f4ee;
  border-radius: 20px;
}
.accordion .card.is-active + .card {
  border-top-color: transparent;
}
.accordion .card__title {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 3.5rem;
  margin: 0;
  margin-bottom: -1px;
  padding: 0.5rem 1.25rem;
  color: rgba(0, 0, 0, 0.8);
  text-decoration: none;
  font-size: 18px;
  font-weight: 600;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.accordion .card__title:hover .icon {
  opacity: 0.8;
}
.accordion .card__title.is-active .icon {
  transform: rotateX(180deg);
  opacity: 1;
}
.accordion .card__title .icon {
  width: 28px;
  height: 28px;
  fill: currentColor;
  opacity: 0.3;
  transition: 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  margin-right: -0.25rem;
}
.accordion .card__content {
  padding: 0 1.25rem 1.25rem;
}

* {
  box-sizing: border-box;
  line-height: calc(1em + 0.5rem);
}

html {
  font: 16px/1.5 "Nunito Sans", sans-serif;
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
}

h1 {
  margin-bottom: 3rem;
  font-size: 2rem;
  font-weight: 400;
  color: gray;
}

a {
  color: inherit;
  text-decoration: none;
}

a,
[data-toggle] {
  cursor: pointer;
}
