// Статичная карточка
.hero {
  margin-bottom: 100px;

  &__container {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    border-radius: 10px;
    width: 100%;
    height: 617px;
    background-color: #f8f4f0;
  }

  .card__img1 {
    position: absolute;
    left: 0;
    top: 0;
  }

  .card__img2 {
    position: absolute;
    left: 0;
    top: 300px;
    width: 179px;
  }

  .card__img4 {
    position: absolute;
    right: 15%;
    top: -75px;
    width: 179px;
  }

  .card__img5 {
    position: absolute;
    right: 0;
    top: 0;
    width: 179px;
  }

  .card__img6 {
    position: absolute;
    right: 0;
    top: 50%;
    width: 179px;
  }

  .card__img7 {
    position: absolute;
    right: 12%;
    top: 65%;
    width: 30%;
  }

  &__title {
    margin: 0;
    margin-bottom: 10px;
    border-style: solid;
    border-color: transparent;
    font-family: "GraphikLCTT", "Arial", sans-serif;
    font-weight: 600;
    font-size: 100px;
    line-height: 0.9;
    text-align: center;
    color: #1e3a2b;
    background-position: center center;
  }

  &__descr {
    border-color: transparent;
    font-family: "GraphikLCTT", "Arial", sans-serif;
    font-weight: 400;
    font-size: 20px;
    line-height: 1.4;
    text-align: center;
    color: #1e3a2b;
    background-position: center center;
  }

  &__btns {
    display: flex;
    justify-content: center;
  }

  &__course {
    margin-right: 20px;
    border-style: solid;
    border-color: transparent;
    border-radius: 15px;
    padding: 12px 24px;
    font-family: "GraphikLCTT", "Arial", sans-serif;
    font-weight: 500;
    font-size: 20px;
    line-height: 1.55;
    color: #1e3a2b;
    background-position: center center;
    background-color: #d0f87a;
    transition: background-color 0.2s ease-in-out, color 0.2s;
  }

  &__programm {
    border-style: solid;
    border-width: 1px;
    border-color: #1e3a2b;
    border-radius: 15px;
    padding: 12px 24px;
    font-family: "GraphikLCTT", "Arial", sans-serif;
    font-weight: 500;
    font-size: 20px;
    line-height: 1.55;
    color: #1e3a2b;
    background-position: center center;
    transition: background-color 0.2;
  }
}
