.nutritionist {
	margin-bottom: 100px;
	
  &__wrapper {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 24px;
  }

  &__left {
		position: relative;
    grid-column: 6 span;
  }

	&__arrow {
		position: absolute;
		right: 40px;
		width: 485px;
		height: 178px;
		background-image: url('/img/svg/arrow.svg');
		background-size: contain;
		background-repeat: no-repeat;
	}

  &__title {
    margin: 0;
    margin-bottom: 20px;
    border-style: solid;
    border-color: transparent;
    font-family: "GraphikLCTT", "Arial", sans-serif;
    font-weight: 600;
    font-size: 60px;
    line-height: 0.9;
    color: #1e3a2b;
    background-position: center center;
  }

  &__descr {
    border-style: solid;
    border-color: transparent;
    font-family: "GraphikLCTT", "Arial", sans-serif;
    font-weight: 400;
    font-size: 20px;
    line-height: 1.5;
    color: #1e3a2b;
    background-position: center center;
  }

  &__right {
    grid-column: 6 span;
    border-radius: 10px;
    padding: 25px 50px;
    padding-top: 50px;
    background-color: #1e3a2b;
  }

  &__subtitle {
    margin: 0;
    margin-bottom: 40px;
    border-style: solid;
    border-color: transparent;
    font-family: "GraphikLCTT", "Arial", sans-serif;
    font-weight: 600;
    font-size: 40px;
    line-height: 0.9;
    color: #fff;
    background-position: center center;
  }

  &__caption {
    margin: 0;
    border-style: solid;
    border-color: transparent;
    font-family: "GraphikLCTT", "Arial", sans-serif;
    font-weight: 500;
    font-size: 24px;
    line-height: 1.2;
    color: #fff;
    background-position: center center;
  }

  &__text {
    margin: 0;
    margin-bottom: 40px;
    border-style: solid;
    border-color: transparent;
    font-family: "GraphikLCTT", "Arial", sans-serif;
    font-weight: 400;
    font-size: 18px;
    line-height: 1.2;
    color: #fff;
    background-position: center center;
  }

  &__item {
		position: relative;
		padding-left: 30px;

    &::before {
      content: "";
			position: absolute;
			left: 0;
			top: 5px;
			display: inline-block;
			margin-right: 25px;
      width: 20px;
      height: 20px;
      background-image: url("../img/svg/check.svg");
			background-repeat: no-repeat;
    }
  }
}
