.question {
	padding: 100px 0;
	background-color: #1e3a2b;

	&__title {
		text-align: center;
		margin: 0;
		font-size: 52px;
		font-weight: 600;
		color: #fff;
	}

	&__descr {
		text-align: center;
		font-size: 18px;
		line-height: 28px;
		color: #fff;
	}

	&__tel {
		display: block;
		margin-bottom: 15px;
		text-align: center;
		font-size: 36px;
		font-weight: 700;
		color: #fff;
	}

	&__mail {
		display: block;
		text-align: center;
		color: #fff;
		font-size: 28px;
		font-weight: 600;
	}
}