.learning {
  margin-bottom: 100px;

  &__container {
    padding: 0 50px;
  }

  &__title {
    border-style: solid;
    border-color: transparent;
    max-width: 560px;
    font-family: "GraphikLCTT", "Arial", sans-serif;
    font-weight: 600;
    font-size: 60px;
    line-height: 0.9;
    color: #bd9f67;
    background-position: center center;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 28px;
  }

  &__item {
    display: flex;
    flex-direction: column;
    grid-column: 3 span;
    border-radius: 10px;
    padding: 15px;
    height: auto;
    min-height: 261px;
    background-color: grey;
    background-color: #d9b676;
  }

  &__tag {
    border-style: solid;
    border-color: transparent;
    border-radius: 10px;
    padding: 8px;
    min-width: 80px;
    max-width: max-content;
    font-family: "GraphikLCTT", "Arial", sans-serif;
    font-weight: 500;
    font-size: 24px;
    line-height: 1;
    color: #bd9f67;
    background-position: center center;
    background-color: #fff;
  }

  &__descr {
    margin: 0;
    margin-top: auto;
    border-style: solid;
    border-color: transparent;
    font-family: "GraphikLCTT", "Arial", sans-serif;
    font-weight: 400;
    font-size: 20px;
    line-height: 1.4;
    color: #fff;
    background-position: center center;
  }
}
