.registration {
	margin-bottom: 100px;

  &__wrapper {
    display: flex;
    justify-content: space-between;
    padding: 115px;
		
    background-color: #d0f87a;
    border-radius: 10px;
  }

  &__title {
		margin: 0;
    max-width: 450px;
    color: #1e3a2b;
    font-size: 60px;
    font-family: "GraphikLCTT", Arial, sans-serif;
    line-height: 0.9;
    font-weight: 600;
    background-position: center center;
    border-color: transparent;
    border-style: solid;
  }

	&__form {
		width: 100%;
		max-width: 620px;
	}
}

.form {
  display: flex;
  flex-direction: column;
	width: 100%;
	max-width: 620px;

  &__name {
		padding-left: 20px;
		margin-bottom: 15px;
    color: rgb(30, 58, 43);
    border: 1px solid rgb(224, 224, 224);
    background-color: rgb(255, 255, 255);
    border-radius: 10px;
    font-size: 18px;
    font-weight: 400;
    height:55px;

		&::placeholder {
			padding-left: 20px;
		}
  }

	&__mail {
		padding-left: 20px;
		margin-bottom: 15px;
    color: rgb(30, 58, 43);
    border: 1px solid rgb(224, 224, 224);
    background-color: rgb(255, 255, 255);
    border-radius: 10px;
    font-size: 18px;
    font-weight: 400;
    height:55px;

		&::placeholder {
			padding-left: 20px;
		}
	}

	&__tel {
		padding-left: 20px;
		margin-bottom: 15px;
    color: rgb(30, 58, 43);
    border: 1px solid rgb(224, 224, 224);
    background-color: rgb(255, 255, 255);
    border-radius: 10px;
    font-size: 18px;
    font-weight: 400;
    height:55px;

		font-family: inherit;
    font-size: 1rem;
    font-weight: 600;

		&::placeholder {
			padding-left: 20px;
		}
	}

	&__btn {
		width: 360px;
		padding: 20px;
		color: #ffffff;
    border-radius: 10px;
    font-size: 20px;
    font-family: unset;
    cursor: pointer;
    background-color: #1e3a2b;
	}
}
